.footer_container{

    display: flex;
    justify-content: center;
    height: 300px;
    flex-direction: column;
    align-items: center;
    background-color: #294d83;
    gap: 10px;
    font-family: "SF Mono","Fira Code","Fira Mono","Roboto Mono",monospace;
    color: #f4f4f4;

}

.footer_info{
    display: flex;
    flex-direction: row;
    gap:0;
}

.letter{
    width:90px;
    height:80px;
}

.s{
    background: url('/src/Img/s.gif') no-repeat;
    background-size: cover;
}

.u{
    background: url('/src/Img/u.gif') no-repeat;
    background-size: cover;
}
.d{
    background: url('/src/Img/d.gif') no-repeat;
    background-size: cover;
}
.i{
     background: url('/src/Img/i.gif') no-repeat;
    background-size: cover;
}
.p{
    background: url('/src/Img/p.gif') no-repeat;
    background-size: cover;
}

.title_footer, .footer_email{
    
    display: block;
    font-size: 1.25rem;
    line-height: 1.5;
    letter-spacing: 1.75px;
    font-weight: 400;
    text-align: center;

}

.social_icon{

    margin: 2px;
    padding:2px;
    font-size: 2rem;
    position: relative;
    top: 0;
    transition: top ease 0.5s;
    color: #f4f4f1;

}

.social_icon:hover{

    top: -15px;
    color:#9dfaff;
    

}

@media (max-width:500px) {
    .letter{
    width:60px;
    height:60px;
}
}




